import React, { Component } from 'react';
import Switch from './switch';

class Opcoes extends Component {

  constructor(props){
    super();
    this.state = {
      qualidade: false,
      rapidez: false,
      barato: false
    }
  }

  toggleSwitch(field) {
    const change = (prevState) => ({ [field]: !prevState[field] });
    const cb = () => this.runLogic(field);
    this.setState( change, cb );
  };

  runLogic(field){
    let {qualidade, rapidez, barato} = this.state;
    if(field === 'qualidade' && rapidez && barato) rapidez = false;
    else if(field === 'rapidez' && qualidade && barato) barato = false;
    else if(field === 'barato' && rapidez && qualidade) qualidade = false;
    this.setState({ qualidade, rapidez, barato });
  }

  render(){
    return(
      <div className="Opcoes flex vertical">
        <Switch
          className="orange"
          value={this.state.qualidade}
          onChange={() => this.toggleSwitch('qualidade')}
          label="Qualidade" />
        <Switch
          className="red"
          value={this.state.rapidez}
          onChange={() => this.toggleSwitch('rapidez')}
          label="Rapidez" />
        <Switch
          value={this.state.barato}
          onChange={() => this.toggleSwitch('barato')}
          label="Barato" />
      </div>
    );
  }
}

export default Opcoes;



