import React from 'react';
import SwitchComponent from 'react-toggle-switch';
import "../../node_modules/react-toggle-switch/dist/css/switch.min.css"

export default ({value, label, onChange,className}) => (
  <div className="flex horizontal">
    <div className="flex flex-center">
      <SwitchComponent className={className} onClick={onChange} on={value} />
    </div>
    &nbsp;
    <h3>{label}</h3>
  </div>
);


